/** Media Queries **/
html {
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0 0 3rem 0;
  font-family: "Poppins", sans-serif;
  font-size: 0.938rem;
  background-color: hsl(216deg, 60%, 99%);
  color: hsl(216deg, 8%, 45%);
}

h1,
h2,
h3,
h4 {
  color: hsl(216deg, 8%, 15%);
  font-size: 600;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.contenido-centrado {
  max-width: 80rem;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.seccion {
  padding: 2rem 0 4rem;
}

.seccion__titulo {
  font-size: 1.5rem;
}

.seccion__subtitulo {
  display: block;
  font-size: 0.813rem;
  margin-bottom: 3rem;
}

.seccion__titulo,
.seccion__subtitulo {
  text-align: center;
}

.contenedor {
  max-width: 768px;
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.header {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
  background-color: hsl(216deg, 60%, 99%);
}

.nav {
  max-width: 968px;
  height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo,
.nav__toggle {
  color: hsl(216deg, 8%, 15%);
  font-weight: 500;
}

.nav__logo:hover {
  color: hsl(216deg, 69%, 61%);
}

.nav__toggle {
  font-size: 1.1rem;
  cursor: pointer;
}

.nav__toggle:hover {
  color: hsl(216deg, 69%, 61%);
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: hsl(216deg, 60%, 99%);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
  }
}
.nav__list {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.813rem;
  color: hsl(216deg, 8%, 15%);
  font-weight: 500;
}

.nav__link:hover {
  color: hsl(216deg, 69%, 61%);
}

.nav__icon {
  font-size: 1.2rem;
}

.nav__close {
  position: absolute;
  right: 1.3rem;
  bottom: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  color: hsl(216deg, 8%, 15%);
}

.nav__close:hover {
  color: hsl(216deg, 69%, 61%);
}

/*Ense単ar el menu*/
.mostrar-menu {
  bottom: 0;
}

.inicio__contenedor {
  gap: 1rem;
}

.inicio__contenido {
  grid-template-columns: 0.5fr 3fr;
  padding-top: 3.5rem;
  align-items: center;
}

.inicio__social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}

.inicio__social-icon {
  font-size: 1.25rem;
  color: hsl(216deg, 69%, 61%);
}

.inicio__social-icon:hover {
  color: hsl(216deg, 57%, 53%);
}

.inicio__blob {
  width: 200px;
  fill: hsl(216deg, 69%, 61%);
}

.inicio__blob-img {
  width: 170px;
}

.inicio__datos {
  grid-column: 1/3;
}

.inicio__titulo {
  font-size: 2rem;
}

.inicio__subtitulo {
  font-size: 1.125rem;
  color: hsl(216deg, 8%, 45%);
  font-weight: 500;
  margin-bottom: 0.75rem;
}

.inicio__descripcion {
  margin-bottom: 2rem;
}

.inicio__scroll {
  display: none;
}

.inicio__scroll-button {
  color: hsl(216deg, 69%, 61%);
  transition: 0.3s;
}

.inicio__scroll-button:hover {
  transform: translateY(0.35rem);
}

.inicio__scroll-mouse {
  font-size: 2rem;
}

.inicio__scroll-name {
  font-size: 0.813rem;
  color: hsl(216deg, 8%, 15%);
  font-weight: 500;
  margin-right: 0.25rem;
}

.inicio__scroll-arrow {
  font-size: 1.25rem;
}

.button {
  display: inline-block;
  background-color: hsl(216deg, 69%, 61%);
  color: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
}

.button:hover {
  background-color: hsl(216deg, 57%, 53%);
}

.button__icon {
  font-size: 1.25rem;
  margin-left: 0.5rem;
  transition: 0.3s;
}

.button--flex {
  display: inline-flex;
  align-items: center;
}

.button--small {
  padding: 0.75rem 1rem;
}

.button--link {
  padding: 0;
  background-color: transparent;
  color: hsl(216deg, 69%, 61%);
}

.button--link:hover {
  background-color: transparent;
  color: hsl(216deg, 57%, 53%);
}

.active-link {
  color: hsl(216deg, 69%, 61%);
}

.scroll-header {
  box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.5);
}

.about__img {
  width: 200px;
  border-radius: 1rem;
  justify-self: center;
  align-self: center;
}

.about__descripcion {
  text-align: center;
  margin-bottom: 2.5rem;
}

.about__info {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 2.5rem;
}

.about__info-titulo {
  font-size: 1.25rem;
  font-weight: 600;
  color: hsl(216deg, 69%, 61%);
}

.about__info-name {
  font-size: 0.75rem;
}

.about__info-name,
.about__info-titulo {
  display: block;
  text-align: center;
}

.about__buttons {
  display: flex;
  justify-content: center;
}

.habilidades__contenedor {
  row-gap: 0;
}

.habilidades__header {
  display: flex;
  align-items: center;
  margin-bottom: 2.5rem;
  cursor: pointer;
}

.habilidades__contenido {
  margin-bottom: 0.75rem;
}

.habilidades__icon,
.habilidades__arrow {
  font-size: 2rem;
  color: hsl(216deg, 69%, 61%);
}

.habilidades__icon {
  margin-right: 0.75rem;
}

.habilidades__titulo {
  font-size: 1.125rem;
}

.habilidades__arrow {
  margin-left: auto;
  transition: 0.4s;
}

.habilidades__list {
  row-gap: 1.5rem;
  padding-left: 2.7rem;
}

.habilidades__titulos {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.habilidades__name {
  font-size: 0.938rem;
  font-weight: 500;
}

.habilidades__bar,
.habilidades__porcentaje {
  height: 5px;
  border-radius: 0.25rem;
}

.habilidades__bar {
  background-color: hsl(216deg, 92%, 85%);
}

.habilidades__porcentaje {
  display: block;
  background-color: hsl(216deg, 69%, 61%);
}

.habilidades__html {
  width: 75%;
}

.habilidades__css {
  width: 75%;
}

.habilidades__sass {
  width: 60%;
}

.habilidades__gulp {
  width: 20%;
}

.habilidades__js {
  width: 60%;
}

.habilidades__php {
  width: 60%;
}

.habilidades__mysql {
  width: 85%;
}

.habilidades__mongo {
  width: 25%;
}

.habilidad__cerrar .habilidades__list {
  height: 0;
  overflow: hidden;
}

.habilidad__abrir .habilidades__list {
  height: max-content;
  margin-bottom: 2.5rem;
}

.habilidad__abrir .habilidades__arrow {
  transform: rotate(-180deg);
}

.servicios__contenedor {
  gap: 1.5rem;
  grid-template-columns: repeat(2, 1fr);
}

.servicios__contenido {
  position: relative;
  background-color: #fff;
  padding: 3.5rem 0.5rem 1.25rem 1.5rem;
  border-radius: 0.25rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  transition: 0.3s;
}

.servicios__contenido:hover {
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.15);
}

.servicios__icon {
  display: block;
  font-size: 1.5rem;
  color: hsl(216deg, 69%, 61%);
  margin-bottom: 1rem;
}

.servicios__titulo {
  font-size: 1.125rem;
  margin-bottom: 1rem;
  font-weight: 500;
}

.servicios__button {
  cursor: pointer;
  font-size: 0.813rem;
}

.servicios__button:hover .button__icon {
  transform: translateX(0.25rem);
}

.servicios__modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}

.servicios__modal-contenido {
  position: relative;
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 0.5rem;
}

.servicios__modal-servicios {
  row-gap: 1rem;
}

.servicios__modal-servicio {
  display: flex;
}

.servicios__modal-titulo {
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
}

.servicios__modal-cerrar {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 1.5rem;
  color: hsl(216deg, 69%, 61%);
  cursor: pointer;
}

.servicios__modal-icon {
  color: hsl(216deg, 69%, 61%);
  margin-right: 0.25rem;
}

.active-modal {
  opacity: 1;
  visibility: visible;
}

.portafolio__contenedor {
  overflow: initial;
}

.portafolio__contenido {
  padding: 0 1.5rem;
}

.portafolio__img {
  width: 265px;
  border-radius: 0.5rem;
  justify-self: center;
}

.portafolio__titulo {
  font-size: 1.125rem;
  margin-bottom: 0.5rem;
}

.portafolio__descripcion {
  margin-bottom: 0.75rem;
}

.portafolio__button:hover .button__icon {
  transform: translateX(0.25rem);
}

.swiper-button-prev::after,
.swiper-button-next::after {
  content: "";
}

.swiper-portafolio-icon {
  font-size: 2rem;
  color: hsl(216deg, 69%, 61%);
}

.swiper-button-prev {
  left: -0.5rem;
}

.swiper-button-next {
  right: -0.5rem;
}

.swiper-horizontal > .swiper-pagination-bullets {
  bottom: -2.5rem;
}

.swiper-pagination-bullet-active {
  background-color: hsl(216deg, 69%, 61%);
}

.swiper-button-prev,
.swiper-button-next,
.swipper-pagination {
  outline: none;
}

.contacto__contenedor {
  row-gap: 3rem;
}

.contacto__informacion {
  display: flex;
  margin-bottom: 2rem;
}

.contacto__icon {
  font-size: 2rem;
  color: hsl(216deg, 69%, 61%);
  margin-right: 0.75rem;
}

.contacto__titulo {
  font-size: 1.125rem;
  font-weight: 500;
}

.contacto__subtitulo {
  font-size: 0.813rem;
  color: hsl(216deg, 8%, 65%);
}

.contacto__contenido {
  background-color: hsl(216deg, 70%, 96%);
  border-radius: 0.5rem;
  padding: 0.75rem 1rem 0.25rem;
}

.contacto__label {
  font-size: 0.75rem;
  color: hsl(216deg, 8%, 15%);
}

.contacto__input {
  width: 100%;
  background-color: hsl(216deg, 70%, 96%);
  color: hsl(216deg, 8%, 45%);
  font-family: "Poppins", sans-serif;
  font-size: 0.938rem;
  border: none;
  outline: none;
  padding: 0.25rem 0.5rem 0.5rem 0;
}

.footer {
  padding-top: 2rem;
}

.footer__contenedor {
  row-gap: 3.5rem;
}

.footer__bg {
  background-color: hsl(216deg, 69%, 61%);
  padding: 2rem 0 3rem;
}

.footer__titulo {
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
}

.footer__subtitulo {
  font-size: 0.813rem;
}

.footer__links {
  display: flex;
  flex-direction: column;
  row-gap: 1.5rem;
}

.footer__link:hover {
  color: hsl(216deg, 92%, 85%);
}

.footer__social {
  font-size: 1.25rem;
  margin-right: 1.5rem;
}

.footer__social:hover {
  color: hsl(216deg, 92%, 85%);
}

.footer__copy {
  font-size: 0.75rem;
  text-align: center;
  color: hsl(216deg, 92%, 85%);
  margin-top: 3rem;
}

.footer__titulo,
.footer__subtitulo,
.footer__link,
.footer__social {
  color: #fff;
}

.scrollup {
  position: fixed;
  right: 1rem;
  bottom: -20%;
  background-color: hsl(216deg, 69%, 61%);
  opacity: 0.8;
  padding: 0 0.3rem;
  border-radius: 0.4rem;
  z-index: 10;
  transition: 0.4s;
}

.scrollup:hover {
  background-color: hsl(216deg, 57%, 53%);
}

.scrollup__icon {
  font-size: 1.5rem;
  color: #fff;
}

.show-scroll {
  bottom: 5rem;
}

::-webkit-scrollbar {
  width: 0.6rem;
  background-color: hsl(216deg, 12%, 90%);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: hsl(216deg, 69%, 61%);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: hsl(216deg, 8%, 65%);
}

@media screen and (max-width: 390px) {
  .contenedor {
    margin-left: 1rem;
    margin-right: 1rem;
  }
  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }
  .nav__list {
    column-gap: 0;
  }
  .inicio__contenido {
    grid-template-columns: 0.25fr 3fr;
  }
  .inicio__blob {
    width: 180px;
  }
  .habilidades__titulo {
    font-size: 0.938rem;
  }
  .servicios__contenedor {
    grid-template-columns: max-content;
    justify-content: center;
  }
  .servicios__contenido {
    padding-right: 3.5rem;
  }
  .servicios__modal {
    padding: 0 0.5rem;
  }
}
@media screen and (min-width: 568px) {
  .inicio__contenido {
    grid-template-columns: max-content 1fr 1fr;
  }
  .inicio__datos {
    grid-column: initial;
  }
  .inicio__img {
    order: 1;
    justify-self: center;
  }
  .contacto__contenedor {
    display: flex;
    justify-content: center;
  }
  .about__contenedor,
  .portafolio__contenido,
  .footer__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 760px) {
  .contenedor {
    margin-left: auto;
    margin-right: auto;
  }
  body {
    margin: 0;
  }
  .section {
    padding: 6rem 0 2rem;
  }
  .section__subtitulo {
    margin-bottom: 4rem;
  }
  .header {
    top: 0;
    bottom: initial;
  }
  .header,
  .main,
  .footer__contenedor {
    padding: 0 1rem;
  }
  .nav {
    height: 4.5rem;
    column-gap: 1rem;
  }
  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: none;
  }
  .nav__list {
    display: flex;
    column-gap: 2rem;
  }
  .nav__menu {
    margin-left: auto;
  }
  .inicio__contenedor {
    row-gap: 5rem;
  }
  .inicio__contenido {
    padding-top: 5.5rem;
    column-gap: 2rem;
  }
  .inicio__blob {
    width: 270px;
  }
  .inicio__scroll {
    display: block;
  }
  .inicio__scroll-button {
    margin-left: 3rem;
  }
  .about__contenedor {
    column-gap: 5rem;
  }
  .about__img {
    width: 350px;
  }
  .about__descripcion {
    text-align: justify;
  }
  .about__info {
    justify-content: space-evenly;
  }
  .about__buttons {
    justify-content: initial;
  }
  .servicios__contenedor {
    grid-template-columns: repeat(2, 218px);
    justify-content: center;
  }
  .servicios__icon {
    font-size: 2rem;
  }
  .servicios__contenido {
    padding: 6rem 0 2rem 2.5rem;
  }
  .servicios__modal-contenido {
    width: 450px;
  }
  .portafolio__img {
    width: 420px;
  }
  .portafolio__contenido {
    align-items: center;
  }
  .contacto__contenedor {
    display: flex;
    justify-content: center;
  }
  .footer__contenedor {
    grid-template-columns: repeat(3, 1fr);
  }
  .footer__bg {
    padding: 3rem 0 3.5rem;
  }
  .footer__links {
    flex-direction: row;
    column-gap: 2rem;
  }
  .footer__socials {
    justify-self: flex-end;
  }
  .footer__copy {
    margin-top: 4.5rem;
  }
}
@media screen and (min-width: 1020px) {
  .header,
  .main,
  .footer__contenedor {
    padding: 0;
  }
  .inicio__blob {
    width: 320px;
  }
  .servicios__contenedor {
    grid-template-columns: repeat(2, 238px);
  }
  .portafolio__contenido {
    column-gap: 5rem;
  }
  .swiper-portafolio-icon {
    font-size: 3.5rem;
  }
  .swiper-button-prev {
    left: -3.5rem;
  }
  .swiper-button-next {
    right: -3.5rem;
  }
  .habilidades__contenedor {
    max-width: 500px;
  }
}
.nav__btns {
  display: flex;
  align-items: center;
}

.change-theme {
  font-size: 1.25rem;
  color: hsl(216deg, 8%, 15%);
  margin-right: 1rem;
  cursor: pointer;
}

.change-theme:hover {
  color: hsl(216deg, 69%, 61%);
}

body.dark-theme {
  background-color: hsl(216deg, 28%, 12%);
}
body.dark-theme h1,
body.dark-theme h2,
body.dark-theme h3,
body.dark-theme h4,
body.dark-theme p,
body.dark-theme span,
body.dark-theme .uil-moon,
body.dark-theme .uil-apps,
body.dark-theme .uil-times,
body.dark-theme a {
  color: white;
}
body.dark-theme header,
body.dark-theme .nav__menu,
body.dark-theme .servicios__modal-contenido {
  background-color: hsl(216deg, 29%, 16%);
  color: white;
}
body.dark-theme .servicios__contenido {
  background-color: hsl(216deg, 30%, 8%);
}
body.dark-theme .swiper-pagination-bullet {
  background-color: white;
}
body.dark-theme .footer__bg {
  background-color: hsl(216deg, 29%, 16%);
}